:root{
  --primary-light-gray:#f4f4f4;
  --primary-white:#ffffff;
  --primary-blue:#84bbc8;
 --primary-dark1:#282c34;
  --primary-dark:rgb(52,58,64);
  --primary-red:#ef4035;
  --primary-hover-red:#f33501;
  --primary-gray:#ccd1d9;
  --primary-dark-gray:#666666;
  --primay-black:#000000;
  --primay-transparent-black: rgba(0,0,0,0.7);
  --primary-green:green;

}
body{
  font-family: "Roboto condensed",sans-serif !important;
 
}
.logo{width:16.25rem;}
nav { z-index:2;}
/*  links navbar*/
.nav a.nav-link{

 
letter-spacing: 0.0375rem;
cursor:pointer;

}
nav ul li{ text-transform: uppercase
  ;}

  .navbar-nav > li > a{
   margin-right: 0.75rem;
    margin-left: 0.75rem;
    color:#ffffff!important;
    color:var(--primary-white)!important;
    border-bottom: 0.1870rem solid rgb(52,58,64);
    border-bottom: 0.1870rem solid var(--primary-dark);
  }
