    }
   }
   .photo-wrap{

    display:-webkit-flex;

    display:flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    
   }

   .profile-img{

    /* width:16rem; */
    /* border-radius: 50%; */
    padding:.3rem;
     border:1px solid #84bbc8;
     border:1px solid var(--primary-blue)
   }

   .about-heading{

    text-transform: uppercase;
    color:#84bbc8;
    color:var(--primary-blue);
   }

   @media(max-width:468px){

    .profile-img{

