   /**********services********/

   .services{
     text-align: center;
     padding-bottom: 2rem;
     background-color: #f4f4f4;
     background-color: var(--primary-light-gray);

   }
   .services h1{
     color:rgb(52,58,64);
     color:var(--primary-dark);
   }

   .services .circle{
   position: relative;
   margin: 0.375rem auto;
   background :#84bbc8;
   background :var(--primary-blue);
   border-radius: 50%;
   width:3.125rem;
   height:3.125rem;
  }

  .services .icon{
    font-size:1.4rem;
    color:#ffffff;
    color:var(--primary-white);
    position: absolute;
    top:50%;
    left:50%;
    -webkit-transform:translate(-50%, -50%);
            transform:translate(-50%, -50%);
  }
  .services .box{
    height:16rem;
    border-bottom: 0.3125rem solid #84bbc8;
    border-bottom: 0.3125rem solid var(--primary-blue);
    padding:0.625rem;
    margin-bottom: 1.875rem;
    background:#ffffff;
    background:var(--primary-white);
    transition: 0.3s ease-in-out;
  }

  .services .box:hover{

    background: #84bbc8;

    background: var(--primary-blue);
    border-bottom: 0.3125rem solid rgb(52,58,64);
    border-bottom: 0.3125rem solid var(--primary-dark);
  }
  .services .box:hover .icon{
    color:rgb(52,58,64);
    color:var(--primary-dark);
  }
  .services .box:hover .circle{
    background-color:#ffffff;
    background-color:var(--primary-white);
  }

  /* experience */
  .experience {
    background-color: #f4f4f4;
    background-color: var(--primary-light-gray);
    padding:0.7rem;
  }
  .experience-wrapper{
    position: relative;
    padding:3.125rem 0;
    overflow: hidden;
  }
  .experience h1{
    text-transform: uppercase;
    color:#84bbc8;
    color:var(--primary-blue);
  }
  .experience-wrapper:before{
  position: absolute;
  content:"";
  top:0;
  left:50%;
  margin-left:-0.0625rem;
  width:0.125rem;
  height: 100%;
  background:#ccd1d9;
  background:var(--primary-gray);
  z-index:1;

  }
  .timeline-block{
    width: calc(50% + 0.5rem);
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    clear: both;

  }
  .timeline-block-right{ 
     float:right;
  }
  .timeline-block-left{
    float:left;
    direction: rtl;
  }
  .marker{

    width:1rem;
    height:1rem;
    border-radius: 50%;
    border:0.3125rem solid rgb(52,58,64);
    border:0.3125rem solid var(--primary-dark);
    background: #84bbc8;
    background: var(--primary-blue);
    margin-top: 0.625rem;
    z-index:3
  }

  .timeline-content{

    width:95%;
    padding:0.09375rem;
    color:#666666;
    color:var(--primary-dark-gray);
    direction: ltr;
  }
  .timeline-content h3{
margin: 0.3125rem;
color:#ef4035;
color:var(--primary-red);
font-weight: 900;

  }

  @media (max-width:768px){
    .experience .container{

      padding-left:0
    }

    .experience-wrapper:before{

      left:0.5rem;
      margin-bottom: 1.875rem;
    }

    .timeline-block-right{ 
      float:none;
      width:100%;

   }
   .timeline-block-left{
     float:none;
     direction: ltr;
     width:100%;
   }


  }

  /* GALLERY */

  .testimonials {

    /* background-image: url(); */
    background-size:cover;
    /* background-color:var(--primary-gray); */
    text-align:center;
    padding-bottom: 0;
